.card {
  margin: 10px;
  padding: 10px;
  text-align: justify;
}

#submit-mood {
  margin: 15px;
}

#happy-button {
  color: black;
  background-color: yellow;
  border: 3px solid rgb(230, 230, 1);
  margin: 5px;
}

#sad-button {
  color: black;
  background-color: rgb(0, 81, 255);
  border: 3px solid rgb(5, 75, 226);
  margin: 5px;
}

#excited-button {
  color: black;
  background-color: rgb(51, 255, 0);
  border: 3px solid rgb(42, 209, 0);
  margin: 5px;
}

#angry-button {
  color: black;
  background-color: rgb(245, 6, 6);
  border: 3px solid rgb(212, 4, 4);
  margin: 5px;
}

#meh-button {
  color: black;
  background-color: rgb(245, 94, 6);
  border: 3px solid rgb(211, 78, 1);
  margin: 5px;
}

#update-button {
  color: gray;
  background-color: purple;
  border: 3px solid black;
  margin: 5px;
}

#delete-button {
  color: black;
  background-color: gray;
  border: 3px solid purple;
  margin: 5px;
}

#mood-container {
  display: block;
}

#mood-buttons-container {
  display: block;
}

