body {
  /* background: linear-gradient(90deg, lightgreen, lightblue); */
  background: linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
  font-family: 'Pacifico', sans-serif;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: calc(100vh - 100px);
  overflow-y: hidden;
}

a {
  display: -webkit-flex;
  display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    padding: 25px;
}

.container {
  display: -webkit-flex;
  display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.title {
  font-family: 'Pacifico', sans-serif;
  font-size: 88px;
  text-align: center;
  font-weight: 500;
}

#quote-box {
  font-family: 'Lobster', cursive;
  width: 1400px;
  margin: 0 auto;
  padding: 1px 15px;
  font-weight: 550;
  font-size: 44px;
  /* background: white; */
  /* background: linear-gradient(to right, #833ab4, #fd1d1d, #fcb045); */
  /* background: linear-gradient(35deg, #CCFFFF, #FFCCCC); */
  text-align: center;
  border-radius: 20px;
  box-shadow: 0px 0px 2px 1px gray;
}

#text p {
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
  -webkit-margin-after: 0.5em;
          margin-block-end: 0.5em;
}

#author h5 {
  font-family: 'Allura', cursive;
  font-size: 64px;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
}

#buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.button {
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  font-size: 1rem;
  padding: 5px;
  border-radius: 50em;
  box-shadow: 0px 0px 3px .5px rgb(82, 81, 81);
  border: 0;
  margin-bottom: 10px;
}

.button:focus {
  outline: none;
  border: none;
}

.btn-happy, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  background-color: yellow !important;
  color: black;
}

.btn-sad, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  background-color: blue !important;
  color: black;
}

.btn-excited, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  background-color: #2ecc71 !important;
  color: black;
}

.btn-angry, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  background-color: #e74c3c !important;
  color: black;
}

.btn-meh, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  background-color: #e67e22 !important;
  color: black;
}

